.nursery {
	padding: 80px 0;
	font-size: 0;
	&__title {
		text-align: center;
		color: #1f1f1f;
		font-family: Akrobat;
		font-size: 45px;
		font-weight: 400;
		text-transform: uppercase;
		margin-bottom: 66px; }
	&__caption {
		font-size: 0;
		padding: 0;
		position: absolute;
		border: 1px solid #fff;
		left: 10px;
		right: 10px;
		top: 10px;
		bottom: 10px;
		padding: 0;
		margin: 0;
		span {
			position: absolute;
			bottom: 0;
			width: 110%;
			height: 76px;
			font-size: 18px;
			font-family: Akrobat;
			background-color: rgba(255,255,255, .7);
			color: #000;
			text-transform: uppercase;
			line-height: 76px;
			text-align: center;
			-moz-transform: matrix( 0.96147262338163,0,0,0.95973332538691,0,0);
			-webkit-transform: matrix( 0.96147262338163,0,0,0.95973332538691,0,0);
			-ms-transform: matrix( 0.96147262338163,0,0,0.95973332538691,0,0);
			position: absolute; }
		bottom: 19px;
		left: 5px;
		right: 5px; }

	.big-images {
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		.grid-item {
			position: relative;
			width: 25%;
			margin: 10px;
			overflow: hidden;
			&:first-child {
				margin-left: 0; }
			&:last-child {
				margin-right: 0; }
			img {
				width: 100%; } } }
	.small-images-video {
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		.small-images-wrap,
		.video {
			width: 50%;
			margin: 10px; }
		.small-images-wrap {
			margin-left: 0;
			margin-top: 0;
			margin-bottom: 0;
			margin-right: 5px; }
		.video {
			margin-right: 0;
			margin-left: 10px; } }
	.small-images {
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		.grid-item {
			max-width: 50%;
			overflow: hidden;
			margin: 10px 5px 10px 0px;
			img {
				width: 100%; } } }
	.video {
		position: relative;
		overflow: hidden;
		iframe {
			width: 100%;
			height: 100%; } } }

