.small-woods {
	font-size: 0;
	padding: 80px 0 0;
	zoom: 0.8;
	@include r(1200);
	&__wrap {
		position: relative;
		&:after {
			display: block;
			content: '';
			position: absolute;
			border: 1px solid #fff;
			left: 20px;
			right: 20px;
			top: 20px;
			bottom: 20px;
			@include r(700) {
				left: 10px;
				right: 10px;
				top: 10px;
				bottom: 16px; } }
		img {
			width: 100%;
			height: 100%;
			overflow: hidden; } }

	@include r(1400) {
		zoom: 0.7; }
	@include r(930) {
		zoom: 0.5; }
	@include r(700) {
		zoom: 1; }
	.container {
		position: relative; }
	&__item {
		text-align: center;
		padding-top: 200px;
		@include clr;
		position: relative;
		left: -25px;
		@include r(700) {
			padding: 0;
			position: relative;
			left: auto;
			@include clr;
			margin-bottom: 240px; } }
	&__loz {
		display: none;
		@include r(700) {
			display: block;
			position: absolute;
			right: 0;
			top: 0;
			color: #d24a43;
			font-family: KobzarKS;
			font-size: 22px;
			font-weight: 400;
			/* Text style for "на все сос" */
			letter-spacing: 1.32px;
			transform: rotate(-30deg); } }
	&__item + &__item {
		padding-top: 0;
		margin-top: -200px;
		@include r(700) {
			margin: -250px 0 240px; } }
	&__image {
		max-width: 443px;
		position: relative;
		float: left;
		padding-left: 50px;
		color: #000000;
		font-family: Akrobat;
		font-size: 34px;
		text-transform: uppercase;
		z-index: 2;
		top: -105px;
		left: 50px;
		@include r(700) {
			padding: 0;
			position: relative;
			top: auto;
			left: auto;
			bottom: auto;
			right: auto;
			width: 55%;
			margin: 0;
			margin-bottom: 40px; }
		&_bottom {
			float: right;
			left: 0;
			top: auto;
			bottom: 300px;
			@include r(700) {
				position: relative;
				top: auto;
				left: auto;
				bottom: auto;
				right: auto;
				float: left;
				margin: 0; }
			.small-woods__sale {
				@include r(700) {
					display: none; } } }
		img {

			position: relative;
			box-shadow: 0 7px 9px rgba(0, 0, 0, 0.25); } }
	&__sale {
		position: absolute;
		width: 101px;
		height: 101px;
		border: 2px solid #7b7b7b;
		background-color: rgba(255,255,255,.5);
		text-align: center;
		display: block;
		content: '';
		color: #d24a43;
		font-family: Akrobatbold;
		/*semi*/
		font-size: 48px;
		font-weight: bold;
		text-transform: uppercase;
		// Text style for "-50%"
		letter-spacing: 2.28px;
		line-height: 105px;
		top: -55px;
		left: -55px;
		@include r(700) {
			width: 55px;
			height: 55px;
			font-size: 20px;
			line-height: 53px;
			left: auto;
			right: -28px;
			top: -28px; } }
	&__payment {
		display: none;
		@include r(700) {
			display: block;
			position: absolute;
			bottom: 80px;
			right: 30px; } } }
.small-woods__image_bottom .small-woods__sale {
	top: auto;
	left: auto;
	right: -30px;
	bottom: -30px; }
.stock-sw {
	margin-top: 160px;
	background-color: #808080;
	padding: 46px 0 64px;
	width: 379px;
	margin: 0 auto;
	position: relative;
	left: 50px;
	z-index: 1;
	border: 1px solid #000000;
	@include r(700) {
		padding: 20px 0;
		box-sizing:  border-box;
		position: static;
		width: 50%;
		height: 50%;
		margin: 0;
		float: right;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(0, -50%); }
	&__title {
		text-align: center;
		display: block;
		color: #ffffff;
		font-family: Akrobatbold;
		font-size: 42px;
		text-transform: uppercase;
		/* Text style for "акция" */
		letter-spacing: 2.38px;
		@include r(700) {
			font-size: 20px;
			margin-bottom: 5px;
			line-height: 1; }
		&:after {
			display: block;
			content: '';
			width: 190px;
			height: 2px;
			background-color: #3eb5f1;
			margin: 16px auto 20px;
			@include r(700) {
				margin: 8px auto 8px;
				display: none; } } }
	&__only {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 21px;
		font-weight: 400;
		text-transform: uppercase;
		/* Text style for "т, олько Н" */
		letter-spacing: 1.2px;
		/* Text style for "М" */
		letter-spacing: 0.9px;
		margin-bottom: 43px;
		@include r(700) {
			font-size: 14px;
			margin-bottom: 5px; } }

	&__old-price {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 26px;
		font-weight: 400;
		text-decoration: line-through;
		text-transform: uppercase;
		/* Text style for "10 000" */
		letter-spacing: 1.71px;
		margin-bottom: 20px;
		@include r(700) {
			font-size: 14px;
			margin-bottom: 5px; } }
	&__price {
		text-shadow: 3px 4px 5px rgba(0, 0, 0, 0.15);
		color: #fd7979;
		font-family: Akrobat;
		font-size: 37px;
		font-weight: 400;
		text-transform: uppercase;
		/* Text style for "5, 000" */
		letter-spacing: 2.38px;
		margin-bottom: 39px;
		@include r(700) {
			font-size: 18px;
			margin-bottom: 5px; } }
	&__sale {
		color: #ffffff;
		font-family: Akrobat;
		font-size: 20px;
		font-weight: 400;
		text-transform: uppercase;
		/* Text style for "С, КИДКА Д" */
		letter-spacing: 0.86px;
		margin-bottom: 40px;
		@include r(700) {
			font-size: 14px;
			margin-bottom: 30px; } }
	&__button {
		background-color: transparent;
		border: 2px solid #ffd357;
		padding: 14px;
		border-radius: 20px;
		color: #ffffff;
		font-family: Akrobat;
		font-size: 18px;
		font-weight: bold;
		text-transform: uppercase;
		/* Text style for "КУПИТЬ СЕЙ" */
		letter-spacing: 1.17px;
		@include r(700) {
			font-size: 14px;
			padding: 10px 15px; } } }
.small-woods__text {
	padding-left: 80px;
	@include r(700) {
		padding-left: 0px;
		text-align: left;
		font-size: 20px;
		margin-bottom: 5px; } }


.small-woods__item_left {
	@include r(700) {
		top: 100px;
		margin-bottom: 240px;
		.small-woods__image {
			float: right;
			&_bottom {
				float: right; } }
		.stock-sw {
			left: 0;
			right: auto; }
		.small-woods__text {
			text-align: left; }
		.small-woods__loz {
			left: 0;
			right: auto; }
		.small-woods__sale {} } }

